import React, { useContext } from "react";
import { useForm } from "react-hook-form";
import { getFromLocalStorage, setInLocalStorage, getFromWorkbookSettings, setInWorkbookSettings } from "../taskpane";
import { TokenInput } from "./TokenInput";
import { ModelProviderSelect } from "./ModelProviderSelect";
import { HostOverrideInput } from "./HostOverrideInput";
import { DebugContext } from "../context";
import store from "../../stores/store";
type FormInputs = {
  apiKey: string;
  apiHost: string;
  disableCalculationOverride: boolean;
  modelProvider: "groq" | "openai";
  concurrency: number;
};

const loadSettings = async () => {
  const apiKey = getFromLocalStorage("apiKey") || getFromLocalStorage("arborApiToken") || "";
  const apiHostStr = getFromLocalStorage("apiHost");
  const apiHost = apiHostStr && apiHostStr.startsWith("http") ? apiHostStr : "";
  const disableCalculationOverride = (await getFromWorkbookSettings("disableCalculationOverride")) === "true";
  const modelProvider = ((await getFromWorkbookSettings("modelProvider")) as "groq" | "openai") || "openai";
  const concurrency = parseInt(getFromLocalStorage("concurrency")) || 5;
  return { apiKey, apiHost, disableCalculationOverride, modelProvider, concurrency };
};

interface SettingFormProps {
  advanceSettings: boolean;
  onApiKeyChange?: (_apiKey: string) => void;
  onApiHostChange?: (_apiHost: string) => void;
}

const SettingForm: React.FC<SettingFormProps> = ({ onApiKeyChange, onApiHostChange }) => {
  const { isDebug } = useContext(DebugContext);
  const {
    register,
    handleSubmit,
    formState: { isDirty, isSubmitting, isLoading },
    reset,
  } = useForm<FormInputs>({
    defaultValues: async () => await loadSettings(),
  });

  const onSubmit = async (data: FormInputs) => {
    try {
      setInLocalStorage("apiKey", data.apiKey);
      setInLocalStorage("apiHost", data.apiHost);
      setInLocalStorage("concurrency", data.concurrency.toString());
      await setInWorkbookSettings("disableCalculationOverride", data.disableCalculationOverride.toString());
      await setInWorkbookSettings("modelProvider", data.modelProvider);
      store.setState((prev) => ({
        ...prev,
        concurrency: data.concurrency,
      }));
      reset({
        apiKey: data.apiKey,
        apiHost: data.apiHost,
        disableCalculationOverride: data.disableCalculationOverride,
        modelProvider: data.modelProvider,
        concurrency: data.concurrency,
      });
      onApiKeyChange && onApiKeyChange(data.apiKey);
      onApiHostChange && onApiHostChange(data.apiHost);
    } catch (error) {
      console.error("Error saving keys:", error);
    }
  };
  if (isLoading) {
    return null;
  }

  return (
    <>
      <form onSubmit={handleSubmit(onSubmit)} className="w-full p-3 relative">
        <div className="mb-4">
          <h2 className="font-title font-xl mb-1">Your API Key</h2>
          <p className="text-xs">
            Insert the API key generated from your <br />
            account to start using Arbor Chat
          </p>
        </div>
        <TokenInput id="apiKey" register={register} placeholder="API Key..." className="placeholder-color-[#9A99A1]" />
        <div className="md:block duration-200 whitespace-nowrap rounded-lg font-title text-sm transition-colors my-4">
          <label className="font-title text-sm mb-2 flex gap-2 items-start py-2">
            <input type="checkbox" {...register("disableCalculationOverride")} className="ml-1" />
            <span className="text-xs">Disable Calculation Manual Override</span>
          </label>
        </div>
        {isDebug && (
          <>
            <HostOverrideInput id="apiHost" label="API Host Override" register={register} />
            <ModelProviderSelect register={register} hide={false} />
            <div className="mb-4">
              <div className="">
                <label htmlFor="concurrency" className="items-center font-title text-sm mb-2">
                  Concurrency
                </label>
                <input
                  className={`placeholder-color-lightGrey w-full rounded border border-lightGrey p-3 pr-14`}
                  type="text"
                  id="concurrency"
                  {...register("concurrency")}
                />
              </div>
            </div>
          </>
        )}
        <div className="md:block duration-200 whitespace-nowrap rounded font-title text-sm transition-colors mb-8">
          <button
            type="submit"
            className={`w-full text-sm rounded-lg bg-[#162332] px-6 py-2 text-[#ffffff] ${
              isSubmitting || !isDirty ? "bg-[#646b73]" : ""
            }`}
            disabled={isSubmitting || !isDirty}
          >
            {isSubmitting ? "Saving..." : "Confirm"}
          </button>
        </div>
      </form>
    </>
  );
};

export default SettingForm;
