import React, { useState, useEffect } from "react";
import SettingForm from "../components/SettingForm";
import { useNavigate } from "react-router-dom";

const HoverLayer = ({ show }: { show: boolean }) => {
  return (
    <div
      className={`fixed top-0 left-0 w-full h-full bg-white z-50 transition-opacity duration-500 ${show ? "opacity-100" : "opacity-0 pointer-events-none"}`}
    >
      <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 z-50">
        <img alt="background" src="/assets/bg.webp" className="w-[130vw] max-w-[500px]" />
      </div>
      <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 z-50">
        <img
          alt="background"
          decoding="async"
          data-nimg="fill"
          className="object-cover w-[146px] mx-auto"
          src="/assets/logo.png"
          width={146}
        />
      </div>
      <div className="absolute bottom-0 w-full">
        <div className="text-xs text-center p-4">
          All Rights Reserved ©<br />
          2024 Arbor Technologies Limited
        </div>
      </div>
    </div>
  );
};

const Onboarding: React.FC = () => {
  const navigate = useNavigate();
  const [showHoverLayer, setShowHoverLayer] = useState(true);

  useEffect(() => {
    const timer = setTimeout(() => {
      setShowHoverLayer(false);
    }, 1000);

    return () => clearTimeout(timer);
  }, []);

  return (
    <div className="">
      <HoverLayer show={showHoverLayer} />
      <div className="px-3 pt-8">
        <h1 className="text-xl font-[32px] font-medium mb-2 max-w-[268px] font-title">
          <span className="inline-block">Welcome to ArborChat for Excel</span>
        </h1>
      </div>

      <SettingForm
        advanceSettings={false}
        onApiKeyChange={(apiKey) => {
          if (!apiKey) return;
          navigate("/");
        }}
      />
    </div>
  );
};

export default Onboarding;
