import React, { useContext, useEffect, useState, useSyncExternalStore } from "react";
import store from "../../stores/store";
import { DebugContext } from "../context";

const FormulaInUsed = () => {
  const [formulaCount, setFormulaCount] = useState(0);
  const [errorCount, setErrorCount] = useState(0);
  const [isRefreshing, setIsRefreshing] = useState(false);
  const value = useSyncExternalStore(store.subscribe, store.getState);
  const { isDebug } = useContext(DebugContext);

  const calculateFormulaInUsed = () => {
    if (!window.Office || !window.Excel) return;
    if (isRefreshing) return;
    setIsRefreshing(true);
    Excel.run({ delayForCellEdit: true }, async (context) => {
      const activeSheet = context.workbook.worksheets.getActiveWorksheet();
      const range = activeSheet.getUsedRange();
      range.load("formulas");
      range.load("valuesAsJson");
      await context.sync();

      const values = range.valuesAsJson;
      const formulaCount = range.formulas.reduce(
        (acc, row, rowIndex) => {
          row.forEach((colFormula, colIndex) => {
            const value = values[rowIndex][colIndex];
            const isArborValue =
              value &&
              !!value?.["properties"]?.type?.basicValue?.startsWith &&
              value?.["properties"]?.type?.basicValue?.startsWith("ARBOR_");
            const isArborError = value && !!value?.["properties"]?.error;
            const isArborFormula = colFormula && colFormula?.startsWith && colFormula?.startsWith("=ARBOR_");
            const isError = (isArborValue || isArborFormula) && isArborError;
            acc.error += isError ? 1 : 0;
            acc.value += isArborValue ? 1 : 0;
            acc.formula += isArborFormula ? 1 : 0;
          });
          return acc;
        },
        {
          error: 0,
          value: 0,
          formula: 0,
        }
      );

      setFormulaCount(formulaCount.formula);
      setErrorCount(formulaCount.error);
      setIsRefreshing(false);
    }).catch((error) => {
      console.log(error);
      setIsRefreshing(false);
    });
  };
  useEffect(() => {
    if (!window.Office || !window.Excel) return;
    Office.onReady((info) => {
      if (info.host === Office.HostType.Excel) {
        calculateFormulaInUsed();
      }
    });
  }, []);

  useEffect(() => {
    if (!window.Office || !window.Excel) return () => {};
    Office.context.document.addHandlerAsync(Office.EventType.DocumentSelectionChanged, () => {
      calculateFormulaInUsed();
    });

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const handler = async (_args) => {
      calculateFormulaInUsed();
    };
    Excel.run({ delayForCellEdit: true }, async (context) => {
      context.workbook.worksheets.onChanged.add(handler);
      await context.sync();
    }).catch((error) => {
      console.error(error);
    });
    return () => {
      Office.context.document.removeHandlerAsync(Office.EventType.DocumentSelectionChanged);
      Excel.run({ delayForCellEdit: true }, async (context) => {
        context.workbook.worksheets.onChanged.remove(handler);
        await context.sync();
      }).catch((error) => {
        console.error(error);
      });
    };
  }, []);

  const onAbort = () => {
    store.abortAllRequests();
  };
  const canAbort = value.concurrentCount > 0 || value.pendingResolvers.length > 0;

  return (
    <div className="p-3">
      <div className="bg-lightGrey rounded p-1 flex flex-row gap-2 justify-between items-center">
        <div className="flex flex-row gap-2">
          <div className="flex flex-row px-2">
            <span className="text-green text-sm mr-2">{formulaCount}</span>
            <span className="text-sm">Active</span>
          </div>
          <div className="inline-block h-[20px] min-h-[1em] w-[1px] bg-grey"></div>
          <div className="flex flex-row px-2">
            <span className="text-red text-sm mr-2">{errorCount}</span>
            <span className="text-sm">Error</span>
          </div>
        </div>
        <div className="flex flex-row gap-1">
          <button className="bg-grey rounded p-2 text-xs text-darker" onClick={calculateFormulaInUsed}>
            <img
              src="/assets/refresh.png"
              className={`w-4 h-4 ${isRefreshing ? "animate-spin" : ""}`}
              width={16}
              height={16}
              alt="Recalculate number of formulas in use"
            />
          </button>

          <button className="bg-grey rounded p-2 text-xs text-darker" onClick={onAbort} disabled={!canAbort}>
            <img src="/assets/pause.png" width={16} height={16} alt="pause" style={{ opacity: canAbort ? 1 : 0.5 }} />
          </button>
        </div>
      </div>
      {isDebug && (
        <div className="bg-lightGrey p-1 mt-2">
          <div className="flex flex-row gap-2">
            <div className="flex flex-row px-2 flex-1">
              <span className="text-green text-sm mr-2">{value.processed}</span>
              <span className="text-sm">Completed</span>
            </div>
            <div className="inline-block h-[20px] min-h-[1em] w-[1px] bg-grey"></div>
            <div className="flex flex-row px-2 flex-1">
              <span className="text-blue text-sm mr-2">{value.concurrentCount}</span>
              <span className="text-sm">Running</span>
            </div>
          </div>
          <div className="flex flex-row gap-2 mt-1">
            <div className="flex flex-row px-2 flex-1">
              <span className="text-grey text-sm mr-2">{value.pendingResolvers.length}</span>
              <span className="text-sm">Pending</span>
            </div>
            <div className="inline-block h-[20px] min-h-[1em] w-[1px] bg-grey"></div>
            <div className="flex flex-row px-2 flex-1">
              <span className="text-grey text-sm mr-2">{value.recevied}</span>
              <span className="text-sm">Total</span>
            </div>
          </div>
          <div className="flex flex-row gap-2 mt-1">
            <div className="flex flex-row px-2 flex-1">
              <span className="text-grey text-sm mr-2">{value.concurrency}</span>
              <span className="text-sm">Concurrency</span>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default FormulaInUsed;
